          name: '沪DG5060 厢式车 9.6米 10吨 30方',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          namea:'衣龙魁',
          tel:'15064558893',
          name: '沪DG5060 厢式车 9.6米 10吨 30方',
          address: '上海市普陀区金沙江路 1518 弄'
        }],
        dialogTableVisible: false,
        dialogFormVisible: false,
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        formLabelWidth: '120px'
      };
    },
    mounted(){
      console.log(this.gridData[1].date)
    }
}
</script>

